<template>
  <div class="pos">
    <el-row>
    	<el-col :span="7" class="pos-order" id="order-list">
    		<el-tabs>
    			<el-tab-pane label="点餐">
    				<el-table :data="tableData" border>
    					<el-table-column prop="goodsName" label="商品名称"></el-table-column>
    					<el-table-column prop="count" label="量" width="50"></el-table-column>
    					<el-table-column prop="price" label="金额" width="70"></el-table-column>
    					<el-table-column label="操作" width="100" fixed="right">
    						<template scope="scope">
    							<el-button type="text" size="small" @click="delSingleGoods(scope.row)">删除</el-button>
    							<el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
    						</template>
    					</el-table-column>
    				</el-table>
    				<div class="totalDiv">
    					<small>数量：</small>{{totalCount}} &nbsp;&nbsp;&nbsp; <small>金额：</small>{{totalMoney}}元
    				</div>
    				<div class="btns">
    					<el-button type="warning">挂单</el-button>
    					<el-button type="danger" @click="delAllGoods">删除</el-button>
    					<el-button type="success" @click="checkout">结账</el-button>
    				</div>
    			</el-tab-pane>
    			<el-tab-pane label="挂单">
    				挂单
    			</el-tab-pane>
    			<el-tab-pane label="外卖">
    				外卖
    			</el-tab-pane>
    		</el-tabs>

    	</el-col>
    	<el-col :span="17">
    		<div class="often-goods">
    			<div class="often-goods-tit">常用商品</div>
    			<div class="often-goods-list">
					<ul>
						<li v-for="goods in oftenGoods" @click="addOrderList(goods)">
							<span>{{goods.goodsName}}</span>
							<span class="o-price">￥{{goods.price}}元</span>
						</li>
					</ul>
    			</div>
    		</div>
    		<div class="goods-type">
    			<el-tabs>
    				<el-tab-pane label="汉堡">
						<ul class="cookList">
							<li v-for="type in type0Goods" @click="addOrderList(type)">
						        <span class="foodImg"><img :src="type.goodsImg" width="100%"></span>
						        <span class="foodName">{{type.goodsName}}</span>
						        <span class="foodPrice">￥{{type.price}}元</span>
						    </li>
						</ul>
    				</el-tab-pane>
    				<el-tab-pane label="小食">
    					<ul class="cookList">
							<li v-for="type in type1Goods" @click="addOrderList(type)">
						        <span class="foodImg"><img :src="type.goodsImg" width="100%"></span>
						        <span class="foodName">{{type.goodsName}}</span>
						        <span class="foodPrice">￥{{type.price}}元</span>
						    </li>
						</ul>
    				</el-tab-pane>
    				<el-tab-pane label="饮料">
    					<ul class="cookList">
							<li v-for="type in type2Goods" @click="addOrderList(type)">
						        <span class="foodImg"><img :src="type.goodsImg" width="100%"></span>
						        <span class="foodName">{{type.goodsName}}</span>
						        <span class="foodPrice">￥{{type.price}}元</span>
						    </li>
						</ul>
    				</el-tab-pane>
    				<el-tab-pane label="套餐">
    					<ul class="cookList">
							<li v-for="type in type3Goods" @click="addOrderList(type)">
						        <span class="foodImg"><img :src="type.goodsImg" width="100%"></span>
						        <span class="foodName">{{type.goodsName}}</span>
						        <span class="foodPrice">￥{{type.price}}元</span>
						    </li>
						</ul>
    				</el-tab-pane>
    			</el-tabs>
    		</div>
    	</el-col>
    </el-row>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'pos',
  data(){
  	return{
  		tableData:[],
        oftenGoods:[],
        type0Goods:[],
        type1Goods:[],
        type2Goods:[],
        type3Goods:[],
        totalMoney:0,
        totalCount:0
  	}
  },
  created(){
  	axios.get('https://d-gitlong.github.io/vue-pos/static/js/oftenGoods.json')
  	.then(response=>{
  		//console.log(response)
  		this.oftenGoods=response.data;
  	})
  	.catch(error=>{
  		//console.log(error);
  		alert('网络错误，不能访问');
  	})

  	axios.get('https://d-gitlong.github.io/vue-pos/static/js/typeGoods.json')
  	.then(response=>{
  		this.type0Goods=response.data[0];
  		this.type1Goods=response.data[1];
  		this.type2Goods=response.data[2];
  		this.type3Goods=response.data[3];
  	})
  	.catch(response=>{
  		//console.log(error);
  		alert('网络错误，不能访问');
  	})
  },
  mounted() {
  	var orderHeight=document.body.clientHeight;
  	document.getElementById('order-list').style.height=orderHeight+'px';
  },
  methods:{
  	addOrderList(goods){
  		this.totalMoney = 0;
  		this.totalCount = 0;

  		//商品是否存在订单列表中
  		let isHave = false;
  		for(let i=0;i<this.tableData.length;i++){
  			if (this.tableData[i].goodsId==goods.goodsId) {
  				isHave=true;
  			}
  		}
  		//根据判断的值编写业务逻辑
  		if (isHave) {
  			//改变列表中商品的数量
  			let arr = this.tableData.filter(o=>
  				o.goodsId == goods.goodsId
  			)
  			arr[0].count++;

  		}else{
  			let newGoods={goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1};
  			this.tableData.push(newGoods);
  		}
  		this.getAllMoney();
  	},
  	//删除单个商品
  	delSingleGoods(goods){
  		this.tableData=this.tableData.filter(o=>{
  			console.log(goods);
  			return o.goodsId!=goods.goodsId
  		});
  		this.getAllMoney();
  	},
  	delAllGoods(){
  		this.tableData=[];
  		this.totalCount=0;
  		this.totalMoney=0;
  	},
  	//模拟结账
  	checkout(){
  		if(this.totalCount!=0){
  			this.tableData=[];
  			this.totalCount=0;
  			this.totalMoney=0;
  			this.$message({
  				message:'结账成功！感谢你又为店主出了一份力',
  				type:'success'
  			})
  		}else{
  			this.$message.error('您还未购买商品，欢迎下单！')
  		}
  	},
  	//汇总数量金额
  	getAllMoney(){
  		this.totalMoney=0;
  		this.totalCount=0;
  		if(this.tableData){
  			// 计算汇总金额和数量
	  		this.tableData.forEach((element)=>{
	  			this.totalCount+=element.count;
	  			this.totalMoney=this.totalMoney+(element.price*element.count);
	  		})
  		}
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pos-order{background: #f9fafc; border-right: 1px solid #c0ccda;}
.btns{margin-top: 10px; text-align: center;}
.often-goods-tit{height: 20px; border-bottom: 1px solid #d3dce6; background: #f9fafc; padding: 10px;}
.often-goods-list li{float: left; border: 1px solid #58b7ff; display: block; padding: 10px; margin: 10px; background: #fff; cursor: pointer;}
.o-price{color: #58b7ff;}
.goods-type{clear: both;}
.goods-type ul{margin-left: 2%;}
.cookList li{
   cursor: pointer;
   width:23%;
   border:1px solid #E5E9F2;
   height: auto;
   overflow: hidden;
   background-color:#fff;
   padding: 2px;
   float:left;
   margin: 2px;
}
.cookList li span{
   
    display: block;
    float:left;
}
.foodImg{
   width: 40%;
}
.foodName{
   font-size: 18px;
   width: 60%;
   color:brown;
   text-align: center;
}
.foodPrice{
   font-size: 16px;
   padding-left: 10px;
   padding-top:10px;
}
.totalDiv{
	background: #fff;
	padding: 10px;
	border-bottom: 1px solid #d3dce6
}
</style>



